



















	import goodsOrderItem from '../../components/goodsOrderItem.vue'
	import ut from '../../utils/index.js';
	export default {
		components: {
            goodsOrderItem
        },
		data() {
			return {
				order_list: [],
				cancel_reason: [],
				type: ''
			}
		},
		onLoad(option) {
			this.type = option.type;
			let barTitle = "查看订单";
			if(this.type == 1) {
				barTitle = "查看订单";
			}else if(this.type == 2) {
				barTitle = "配送方案";
			}else if(this.type == 3) {
				barTitle = "搬运议价确认";
			}else if(this.type == 4) {
				barTitle = "建材商品类退换";
			}
			wx.setNavigationBarTitle({
			  title: barTitle
			})
			this.init();
		},
		methods: {
			init() {
				this.getOrderList();
				if(this.type == 1) {
					this.getReasonType();
				}
			},
			getOrderList() {
				ut.request({
					data: {
						type: this.type
					},
					method: 'get',
					url: "goods/order/goodsOrderList"
				}).then(data=>{
					console.log(data)
					data.forEach(item=>{
						item.orderGoods.forEach(item=>{
							if(item.picture)item.picture=item.picture.split(',')[0];
						})
					})
					this.order_list = data;
				})
			},
			getReasonType() {
				ut.request({
					data: {
						type: 3
					},
					method: 'get',
					url: "service/order/cancelReason"
				}).then(data=>{
					console.log(data)
					this.cancel_reason = data;
				})
			}
		}
	}
